import React,{useState,useEffect} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import logo from "../../assets/images/Group 312.png";

/*Navbar of the index page*/
const Navbar = ({setPage}) =>{
  const [isOpen, setIsOpen] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const handleClick = () =>{
    
    window.location.href = "/";
  }

  const components = [
    <div className="navbar-logo" onClick={()=>handleClick()} >
      <img src={logo} alt="" />
    </div>,
    <div className="navbar-logo">
      <span className="navbar-logo-text"><p><span className='orangeColor'>Molecular</span> Computing <br/> Company </p></span>
    </div>
  ]

  const compToggle = () =>{

  }
   
  return (
    <div className = "navbar-container">
      <nav className="navbar">
        {components[activeIndex]}
        <button className="navbar-toggler" onClick={toggleMenu}>
          ☰
        </button>
        <ul className={`navbar-links ${isOpen? 'show':'' }`}>
          <li><a className='other-button' href="#" onClick={()=>{setPage(1);toggleMenu()}}>Company</a></li>
          <li><a className='other-button' href="#contact" onClick={()=>{setPage(0);toggleMenu()}}>Applications</a></li>
          <li><a className='other-button' href="#publications" onClick={()=>{setPage(0);toggleMenu()}}>Technology</a></li>
          <li><a className='other-button' href="#resources" onClick={()=>{setPage(0);toggleMenu()}}>Resources</a></li>
          <li><a className='contact-us' href="#contact" onClick={toggleMenu}>CONTACT US</a></li>
        </ul>
      </nav>
    </div>
  );
}

export default Navbar;