import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import prizes from '../../assets/images/Group 387.png';

import {Row,Col} from "react-bootstrap";

const Prizes = () =>{
    return (
        <div className='prizespagebackground'>
            <Row>
                <Col xs={12}>
                    <img src={prizes}/>
                </Col>
            </Row>
        </div>
    )
}

export default Prizes;