import React from "react";
import "./style.css";

import MolCom from "../../assets/images/Group 342.png";
import PowAlg from "../../assets/images/Group 346.png";
import IPN from "../../assets/images/Group 344.png";
import HealHum from "../../assets/images/Group 351.png";

import {Row,Col} from "react-bootstrap";

const IdeasPage = ()=>{
    return(
        <div className="ideaspagebackground">
            <Row className="justify-content-center text-center">
                <Col xs={6} md={4} lg={3} >
                    <img src={MolCom} className="ideaspageimg"/>
                    <p className="ideaspagetxt">Molecular Computing</p>
                </Col>
                <Col xs={6} md={4} lg={3}>
                    <img src={PowAlg} className="ideaspageimg"/>
                    <p className="ideaspagetxt">Power of algorithms</p>
                </Col>
                <Col xs={6} md={4} lg={3}>
                    <img src={IPN} className="ideaspageimg"/>
                    <p className="ideaspagetxt">Information Processing Networks</p>
                </Col>
                <Col xs={6} md={4} lg={3}>
                    <img src={HealHum} className="ideaspageimg"/>
                    <p className="ideaspagetxt">Healthier Humanity</p>
                </Col>
            </Row>
        </div>
    )
}

export default IdeasPage;