import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import {Row,Col} from "react-bootstrap";
import { Collapse } from 'bootstrap';

import medRxiv from "../../assets/images/Group 201.png";
import siam from "../../assets/images/Group 303.png";

const PublicationsPage = () =>{

    const handleClick = (href) =>{
        window.open(href,"_blank");
    }
    return (
        <div className='publicationspagebackground' id="publications">
            <Row className="justify-content-center text-center">
                <Col className="d-flex flex-column align-items-center">
                    <h2 className='publicationsHeader'>Our Publications</h2>
                    <p className='publicationsText'>Peer reviewed and scientific news on Tapestry and its applications</p>
                    {/* <button className='viewMore'>View More</button> */}
                </Col>
            </Row>
            <Row>
                <Col className="d-flex flex-column align-items-center">
                    <div className='newsContainer'>
                        <Row>
                            <Col xs={6} md={6} lg={6}>
                                <img src={medRxiv}/>
                            </Col>
                            <Col xs={6} md={6} lg={6}>
                                <div className="newsHead" onClick={()=>handleClick("https://www.medrxiv.org/content/10.1101/2020.04.23.20077727v2")}>
                                    <Col xs={12}>
                                        <Row>
                                            <Col xs={2}></Col>
                                            <Col xs={10}>
                                                <p className="date">MAY,2020</p>
                                                <p className='newsHeader'>Tapestry: A Single-Round Smart Pooling Technique for COVID-19 Testing</p>
                                                <p className='newsText'>Finalist round will judge for clinical validation in competition aimed at developing frequent, fast, affordable, and easy-to-use COVID-19 tests, easing global supply chain</p>
                                            </Col>
                                        </Row>
                                    </Col>
                                </div>
                            </Col>
                        </Row>
                        <Row >
                            <Col xs={6} md={6} lg={6} className="justify-content-center text-center">
                                <img src={siam}/>
                            </Col>
                            <Col xs={6} md={6} lg={6} >
                                <div className="newsHead" onClick={()=>handleClick("https://www.siam.org/publications/siam-news/articles/the-mathematics-of-mass-testing-for-covid-19")}>
                                    <Col xs={12}>
                                        <Row>
                                            <Col xs={2}></Col>
                                            <Col xs={10}>
                                                <p className="date">AUGUST,2020</p>
                                                <p className='newsHeader'>The Mathematics of Mass Testing for COVID-19</p>
                                                <p className='newsText'>Finalist round will judge for clinical validation in competition aimed at developing frequent, fast, affordable, and easy-to-use COVID-19 tests, easing global supply chain</p>
                                            </Col>
                                        </Row>
                                    </Col>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={6} md={6} lg={6}>
                                <img src={medRxiv}/>
                            </Col>
                            <Col xs={6} md={6} lg={6}>
                                <div className="newsHead" onClick={() => handleClick("https://www.medrxiv.org/content/10.1101/2020.10.09.20209742v1")}>
                                    <Col xs={12}>
                                        <Row>
                                            <Col xs={2}></Col>
                                            <Col xs={10}>
                                                <p className="date">OCTOBER,2020</p>
                                                <p className='newsHeader'>Large-scale Testing for SARS-CoV-2 using Tapestry Pooling</p>
                                                <p className='newsText'>Finalist round will judge for clinical validation in competition aimed at developing frequent, fast, affordable, and easy-to-use COVID-19 tests, easing global supply chain</p>
                                            </Col>
                                        </Row>
                                    </Col>
                                </div>
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
        </div>
    )
}

export default PublicationsPage;