import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import { Row,Col} from "react-bootstrap";
import Slider from "react-slick";

import anuagrimg from "../../assets/images/Group 302.png";
import vcimg from "../../assets/images/Group 383.png";
import prabimg from "../../assets/images/Group 384.png";
import dhitimg from "../../assets/images/Group 385.png";

import nextImg from "../../assets/images/Vector.png";

const CardSlider = () => {

    const [currentIndex, setCurrentIndex] = useState(0);
    const [isTransitioning, setIsTransitioning] = useState(false);
  
    const infos = [
        {
            img:vcimg,
            text:"Great to see the innovative Tapestry Pooling method of compressed sensing, developed at IITB and prototypes at NCBS and Harvard is recognized [by Nature].",
            head:"Vijay Chandru",
            desig:"Founder, Strand Life Sciences"
        },
        {
            img:prabimg,
            text:"I can, without any doubt, say that your association with us has made the IIITH campus life (academic, research and otherwise) better [in terms of safety].",
            head:"Dr. Prabhakar Bhilampuram",
            desig:"Chairman, Campus Health and Medical Committee, IIITH"
        },
        {
            img:dhitimg,
            text:"Tapestry by Algorithmics Biologics isn't merely a solution; it's an initiative to lead a future where everyone can access the transformative capabilities of NGS, propelling scientific progress while prioritising precision.",
            head:"Dhitiomics",
            design:""
        },
        {
            img:anuagrimg,
            text:"From a public health point of view, such a large-scale screening method that can increase the throughput of labs without need for increased investment or manpower is the need of the hour.",
            head:"Anurag Agrawal",
            desig:"Director, IGIB"
        },
    ]
  
    const handleNavigation = (direction) => {
        if (isTransitioning) return;
    
        setIsTransitioning(true);
        setTimeout(() => {
          setCurrentIndex((prevIndex) =>
            direction === "next"
              ? (prevIndex + 1) % infos.length
              : prevIndex === 0
              ? infos.length - 1
              : prevIndex - 1
          );
          setIsTransitioning(false);
        }, 300); // Match the transition duration
    };

    const handleNext = () => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % infos.length);
    };
  
    const handlePrev = () => {
      setCurrentIndex((prevIndex) =>
        prevIndex === 0 ? infos.length - 1 : prevIndex - 1
      );
    };
  
    return (
      <div className="slider-container">
        <div className={`testimonialcard ${isTransitioning ? "transitioning" : ""}`}>
            <Row>
                <Col xs={12} lg={6} md={6}>
                    <img src={infos[currentIndex].img}/>
                </Col>
                <Col xs={12} lg={6} md={6}>
                    <p className='testcardtext'>{infos[currentIndex].text}</p>
                    <p className='testcardhead'>{infos[currentIndex].head}<br/><span>{infos[currentIndex].desig}</span></p>
                </Col>
            </Row>
        </div>
        <button className="slider-button next" onClick={() => handleNavigation("next")}>
            <img
                src={nextImg}
                alt="Next"
                className="button-image"
            />
        </button>
      </div>
    );
};

const TestimonialsPage = () =>{
    const settings = {
        dots: true, // Show navigation dots
        infinite: true, // Loop through images
        speed: 500, // Transition speed
        slidesToShow: 1, // Show one slide at a time
        slidesToScroll: 1, // Scroll one slide at a time
        arrows: true, // Enable left and right arrows
      };
    return(
        <div className='testimonialspagebackground'>
            <Row>
                <Col xs={12} className="d-flex flex-column align-items-center">
                    <CardSlider />
                </Col>
            </Row>
        </div>
    )
}

export default TestimonialsPage;