import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import symbol from '../../assets/images/Group.png';
import DNA from '../../assets/images/Group 301.png';


import { Container,Row, Col } from 'react-bootstrap';



const ServicePage = () =>{
    
    const handleClick = (href) =>{
        window.open(href,"_blank");
    }

    const services = [
        {
            title:"INNOVATIVE SCIENCE",
            text:"AI for Molecular Computing"
        },
        {
            title:"OUTCOMES",
            text:"Delivers $ Savings for Clients"
        },
        {
            title:"CLOUD DELIVERED",
            text:"User Friendly and Infrastructure Optimal"
        },
        {
            title:"APPROVED",
            text:"CE / IVD Mark"
        },
        {
            title:"PATENTED",
            text:"Technology Stack & Solutions"
        },
        {
            title:"SUPERIOR",
            text:"Quality and Efficiency Outcomes in PCR, NGS, Mass Spectrometry, Assay Design and Drug Discovery"
        }
    ]
    return (
        <div className='servicepagebackground' id="resources">
            <Row>
                <Col>
                    {/* <img src={symbol} className='ServicesImage'/> */}
                    <Row>
                        <Col className='d-flex flex-column align-items-center'>
                            <h1 className='BlogCardHeader'>Latest at Algo<span className='orangeColor'>Bio</span></h1>
                        </Col>
                    </Row>
                    <Row className='flex-column align-items-center'>
                        <Col className='d-flex flex-column align-items-center'>
                            <div className='BlogCard'>
                                {/* <img src={DNA}/> */}
                                <h2>FEATURES</h2>
                                <h3>Algorithmic Biologics in Speciale Invest <br/>DeepTech report</h3>
                                <p>Emerging from academic institutes of national importance</p>
                                <button onClick={()=>handleClick("https://docsend.com/view/fv9jpzq6iv7y8sdq")}>Read More</button>
                                <h2>EVENTS</h2>
                                <h3>AlgoBio participates in NEXUS 2025 alongside JPM Healthcare Conference 2025</h3>
                                <p>Where Innovators Meet Healthcare's Tomorrow</p>
                                <button onClick={()=>handleClick("https://blinkpad.com/nexus/events/nexus2025/agenda")}>Read More</button>
                                <h2></h2>
                                <h3>Meet Algorithmic Biologics at ISHG 2025</h3>
                                <p>NIMHANS Convention Center, Bengaluru, India, January 20-22, 2025</p>
                                <button onClick={()=>handleClick("https://ishg2025.org/")}>Read More</button>
                            </div>
                        </Col>
                    </Row>
                </Col> 
                <Col>
                    {services.map((service)=>{
                        return(
                                <div className='ServicesCard'>
                                    <h3>{ service.title }</h3>
                                    <p>{ service.text}</p>
                                </div>
                        )}
                    )}
                </Col>
            </Row>
        </div>
    )
}

export default ServicePage;