import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import {Row,Col} from "react-bootstrap";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Img1 from "../../assets/images/Group 340.png";
import Img2 from "../../assets/images/Group 339.png";
import Img5 from "../../assets/images/Group 338.png";
import Img4 from "../../assets/images/Group 336.png";
import Img3 from "../../assets/images/Group 337.png";



const SolutionsCard = ({style,img,title,text}) =>{
    return (
        <div className={style}>
            <img src={img} alt="Placeholder Image" className='solutionscard-image'/>
            <div className="soltionscard-content">
                <h3 className="solutionscard-title">{title}</h3>
                <p className="solutionscard-text">{text}</p>
            </div>
        </div>
        )
    }

// Custom Next Arrow
const CustomNextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{
          ...style,
          display: "block",
          background: "white",
          borderRadius: "50%",
          padding: "10px",
          color: "black",
        }}
        onClick={onClick}
      />
    );
  };
  
// Custom Previous Arrow
const CustomPrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <div
        className={className}
        style={{
            ...style,
            display: "block",
            background: "white",
            borderRadius: "50%",
            padding: "10px",
            color: "black",
        }}
        onClick={onClick}
        />
    );
};

const SolutionsPage = () =>{

    const settings = {
        dots: true, // Show navigation dots
        infinite: true, // Infinite scrolling
        speed: 500, // Transition speed in ms
        slidesToShow: 3, // Number of cards to show at once
        slidesToScroll: 1, // Number of cards to scroll at once
        nextArrow: <CustomNextArrow />, // Custom next arrow
        prevArrow: <CustomPrevArrow />,
        // responsive: [
        //   {
        //     breakpoint: 768, // For devices < 768px
        //     settings: {
        //       slidesToShow: 1,
        //     },
        //   },
        //   {
        //     breakpoint: 1024, // For devices < 1024px
        //     settings: {
        //       slidesToShow: 2,
        //     },
        //   },
        // ],
    };

    const cards = [
        {id:1,img:Img1,title:"TAPESTRY FOR NGS",text:"Save 70% on your library preparation. Boost the specificity of your variant calls. Optimize your NGS workflow today with Tapestry for NGS."},
        {id:2,img:Img2,title:"TAPESTRY FOR MASS SPECTROMETRY",text:"Identify the heavy hitter (sample, metabolite) pairs from fewer measurements. Improve the throughput of your mass spectrometry system. Get results for more samples faster."},
        {id:3,img:Img3,title:"ALGOPLEXING - COMPRESSED PCR & ASSAY DESIGN",text:"The fastest assay development technology in the world. Accelerate your multiplexed assay development from years to starting clinical validation in under 4 months. Achieve new efficiencies through the ability of our assay design technology to pack more decisions and more targets into fewer channels with diagnostics-level accuracy."},
        {id:4,img:Img4,title:"TAPESTRY FOR DRUG SCREENING",text:"Are you spending months testing every molecule in your drug library for hits against a target? Discover how you can accelerate your drug discovery pipeline with compressed drug screening from AlgoBio. No new capex, no change to your assays, a solution that works with your existing infrastructure and workflows, and massively reduces your labor."},
        {id:5,img:Img5,title:"COMPRESSED DIFFERENTIAL TRANSCRIPTOMICS ",text:"Transcriptomics is awesome, but it is also very expensive. No more: learn how AlgoBio solutions can bring affordable Transcriptomics to you *Coming Soon*"},
    ]

    return (
        <div className='solutionpagebackground'>
            <Row>
                <Col lg={4} md={4} xs={12}>
                    <h2 className='solutionsHeader'>Explore Our <br/>Solutions</h2>
                </Col>
                <Col lg={8} md={8} xs={0} className="d-flex flex-column align-items-center">
                    <hr className="whiteline2" />
                </Col>
            </Row>
            <Row>
                <Col xs={12}>
                    <Row>
                        <Col>
                            <Row>
                                <Col xs={12} md={6} lg={4} className='d-flex flex-column align-items-center'>
                                    <div id={cards[0].id}>
                                        {/* <SolutionsCard style={"solutionscard1"} img={cards[0].img} title={cards[0].title} text={cards[0].text}/> */}
                                        <div className={"solutionscard1"}>
                                            <img src={cards[0].img} alt="Placeholder Image" className='solutionscard-image'  style={{marginLeft:"-10px"}}/>
                                            <div className="soltionscard-content">
                                                <h3 className="solutionscard-title">{cards[0].title}</h3>
                                                <p className="solutionscard-text">{cards[0].text}</p>
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                                <Col xs={12} md={6} lg={4} className='d-flex flex-column align-items-center'>
                                    <div id={cards[1].id}>
                                        <SolutionsCard style={"solutionscard1"} img={cards[1].img} title={cards[1].title} text={cards[1].text}/>
                                    </div>
                                </Col>
                                <Col xs={12} md={6} lg={4} className='d-flex flex-column align-items-center'>
                                    <div id={cards[4].id}>
                                        <SolutionsCard style={"solutionscard1"} img={cards[4].img} title={cards[4].title} text={cards[4].text}/>
                                    </div>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <br/>
                    <br/>
                    <br/>
                    <Row>
                        <Col xs={0} md={12} lg={2}></Col>
                        <Col xs={12} md={6} lg={4} className='d-flex flex-column align-items-center'>
                            <div id={cards[3].id}>
                                {/* <SolutionsCard style={"solutionscard1"} img={cards[0].img} title={cards[0].title} text={cards[0].text}/> */}
                                <div className={"solutionscard2"}>
                                    <img src={cards[3].img} alt="Placeholder Image" className='solutionscard-image'  style={{marginLeft:"10px"}}/>
                                    <div className="soltionscard-content">
                                        <h3 className="solutionscard-title">{cards[3].title}</h3>
                                        <p className="solutionscard-text">{cards[3].text}</p>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col xs={12} md={6} lg={4} className='d-flex flex-column align-items-center'>
                            <div id={cards[2].id}>
                                <div className={"solutionscard2"}>
                                    <img src={cards[2].img} alt="Placeholder Image" className='solutionscard-image' style={{paddingLeft:"17px"}}/>
                                    <div className="soltionscard-content">
                                        <h3 className="solutionscard-title">{cards[2].title}</h3>
                                        <p className="solutionscard-text">{cards[2].text}</p>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col xs={0} md={2} lg={2}></Col>
                    </Row>
                </Col>
            </Row>
        </div>
    )
}

export default SolutionsPage;