import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import {Row,Col} from "react-bootstrap";

import Manoj from "../../assets/images/TEAMS/Manoj.png";
import Hiran from "../../assets/images/TEAMS/HIRAN.png";
import Prantik from "../../assets/images/TEAMS/PRANTIk.png";
import BARKHA from "../../assets/images/TEAMS/BARKHA.png";
import BHAVNA from "../../assets/images/TEAMS/BHAVNA.png";
import RITA from "../../assets/images/TEAMS/RITA.png";
import ABHI from "../../assets/images/TEAMS/ABHISHEK.png";
import ANIRUDH from "../../assets/images/TEAMS/ANIRUDH.png";
import SHANYA from "../../assets/images/TEAMS/Shanya.png";
import Shubhomita from "../../assets/images/TEAMS/Shubhomita.png";

import AnuAgra from "../../assets/images/Advisors/Anurag Agrawal.png";
import Woolf from "../../assets/images/Advisors/Woolf.png";
import VC from "../../assets/images/Advisors/VC.png";
import Cantor from "../../assets/images/Advisors/Cantor.png";

import Axilor from "../../assets/images/maxresdefault.png";
import Bharat from "../../assets/images/BHARAT.png";

const TeamCard = ({id,image,head,desig}) =>{
    return(
        <div className='teamCard'>
            <img src={image} id={id}/>
            <h3>{head}</h3>
            <p>{desig}</p>
        </div>
    )
}

const AdvisorCard = ({image,name}) =>{
    return (
        <div className='advisorCard'>
            <img src={image}/>
            <h3>{name}</h3>
        </div>
    )
}
const InvestorBox = () =>{
    const handleClick= (href) =>{
        window.open(href,"_blank")
    }
    return(
        <div className='investorBox'>
            <Row>
                <Col xs={4} md={6} lg={6}>
                    <img src={Axilor} id="aximg" />
                </Col>
                <Col xs={8} md={6} lg={6}>
                    <img src={Bharat} id="bhimg"/>
                </Col>
                <Col xs={6} md={6} lg={6}><button onClick={()=>handleClick("https://www.axilor.com/")}>Website</button></Col>
                <Col xs={6} md={6} lg={6}><button id="bhweb" onClick={()=>handleClick("https://bharat.fund/innovation/bif.html")}>Website</button></Col>
            </Row>
        </div>
    )
}

const Peoples = () =>{
    return(
        <div className='teampagebackground'>
            <Row className="justify-content-center text-center">
                <Col xs={12} md={4} lg={4} className='d-flex flex-column align-items-center'></Col>
                <Col xs={12} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <p className='teampagetitle'>Leadership</p>
                </Col>
                <Col xs={12} md={4} lg={4} className='d-flex flex-column align-items-center'></Col>
            </Row>
            <Row className="justify-content-center text-center">
                <Col xs={12} md={12} lg={2}></Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard id={"Manoj"}image={Manoj} head={"MANOJ"} desig={"Founder & CEO"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard id={"Hiran"} image={Hiran} head={"HIRAN"} desig={"CBO"}/>
                </Col>
                <Col xs={12} md={12} lg={2}></Col>
            </Row>
            <br/><br/>
            <Row className="justify-content-center text-center">
                <Col xs={2} md={4} lg={4} className='d-flex flex-column align-items-center'></Col>
                <Col xs={8} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <p className='teampagetitle'>OUR STRENGTH. OUR TEAM.</p>
                </Col>
                <Col xs={2} md={4} lg={4} className='d-flex flex-column align-items-center'></Col>
            </Row>
            <Row className="justify-content-center text-center">
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={Shubhomita} head={"SHUBHAMITA"} desig={"India Head, Sales & BD"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={ANIRUDH} head={"ANIRUDH"} desig={"Molecular Science Specialist"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={BARKHA} head={"BARKHA"} desig={"Project Manager"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={SHANYA} head={"SHANYA"} desig={"Data Scientist"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={BHAVNA} head={"BHAVNA"} desig={"Data Scientist"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={RITA} head={"RITA"} desig={"Data Scientist"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={ABHI} head={"ABHISHEK"} desig={"ML Engineer"}/>
                </Col>
                <Col xs={6} md={4} lg={4} className='d-flex flex-column align-items-center'>
                    <TeamCard image={Prantik} head={"PRANTIK"} desig={"Data Engineer"}/>
                </Col>
            </Row>
            <br/><br/>
            <Row className="justify-content-center text-center">
                <Col xs={4} className='d-flex flex-column align-items-center'></Col>
                <Col xs={4} className='d-flex flex-column align-items-center'>
                    <p className='teampagetitle'>ADVISORS</p>
                </Col>
                <Col xs={4} className='d-flex flex-column align-items-center'></Col>
            </Row>
            <Row className="w-100 justify-content-center text-center">
                <Col xs={6} md={6} lg={3} >
                    <AdvisorCard image={AnuAgra} name={"DR. ANURAG AGRAWAL"} />
                </Col>
                <Col xs={6} md={6} lg={3}>
                    <AdvisorCard image={Cantor} name={"DR. CHARLES CANTOR"} />
                </Col>
                <Col xs={6} md={6} lg={3}>
                    <AdvisorCard image={VC} name={"DR. VIJAY CHANDRU"} />
                </Col>
                <Col xs={6} md={6} lg={3}>
                    <AdvisorCard image={Woolf} name={"DR. PETER WOOLF"} />
                </Col>
            </Row>
            <br/><br/>
            <Row>
                <Col  className='d-flex flex-column align-items-center'></Col>
                <Col xs={4} className='d-flex flex-column align-items-center'>
                    <h1 className='teampagetitle'>INVESTORS</h1>
                </Col>
                <Col xs={4} className='d-flex flex-column align-items-center'></Col>
            </Row>
            <Row className="justify-content-center text-center">
                <Col xs={12} className='d-flex flex-column align-items-center'> 
                    <InvestorBox/>
                </Col>
            </Row>
        </div>
    )
}

export default Peoples;