import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

import {Row,Col} from "react-bootstrap";


const CompanyLandingPage = () =>{
    return (
        <div className='companylandingpagebackground'>
            <Row className="w-100 justify-content-center text-center">
                <Col xs={12} md={8} lg={8}>
                    <p className='companyHeaderHead'>WHAT WE DO</p>
                    {/* <p className='companyHeadingText'>Molecular Computing For Diagnostics & Research</p> */}
                    <p className='companyDescriptionText'>Today we know that all life is made up of small sachets of sentient soup which we call cells. These cells contain DNA which has been called the blueprint of life. A living cell is a bag of molecules obeying chemistry plus the potential to become other bags of molecules encoded in a blueprint. In other words, Biology can be understood as Chemistry plus Algorithms. Chemistry helps understand “what is” and the Algorithmic viewpoint helps us understand “what can become.” There is a practical side to this equation that holds the potential to build technology that the world desperately needs today. We started with a biological cell and understood it as a sum-of-parts consisting of Chemistry and Algorithms. Going the other way, we can ask what can we build by introducing Algorithms into a Chemical milieu.
                    <br/><br/>
                    Len Adleman asked precisely this question in 1994. He encoded an instance of a computational problem known as the Traveling Salesman Problem into molecules of DNA so that the solution to the problem would be revealed by Chemistry. This was a demonstration that there was a path the other way: we can add Algorithms to Chemistry and create systems of molecules that can exhibit behaviour of a sophistication we do not typically associate with Chemistry. This field has come to be known as Molecular Computing.</p>
                    <hr className="whiteline" />
                </Col>
                <Col xs={0} md={4} lg={4}></Col>
            </Row>
        </div>
    )
}

export default CompanyLandingPage;