import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step


import { Container,Row, Col } from 'react-bootstrap';

const AchStats = () =>{
    return(
        <div className="AchStats">
            <Row>
                <Col xs={1}></Col>
                <Col xs={10}>
                    <Row>
                        <Col className='d-flex flex-column align-items-center dotted-col' style={{paddingRight:"15px"}}>
                            <h3 className='StatsHeader' style={{marginLeft:"-70px"}}>20<span className='orangeColor'>+</span></h3>
                            <p className='StatsText' style={{marginLeft:"-10px",width:"150%"}}>Years of Academic Research</p>
                        </Col>
                        <Col className='d-flex flex-column align-items-center dotted-col'style={{paddingLeft:"-2px"}}>
                            <h3 className='StatsHeader'>25,000<span className='orangeColor'>+</span></h3>
                            <p className='StatsText'>Samples Tested</p>
                        </Col>
                        <Col className='d-flex flex-column align-items-center dotted-col'>
                            <h3 className='StatsHeader' style={{marginLeft:"-10px"}}>7<span className='orangeColor'></span></h3>
                            <p className='StatsText' id="pft">Patents Filed</p>
                        </Col>
                        <Col className='d-flex flex-column align-items-center dotted-col' >
                            <h3 className='StatsHeader' id="acc" style={{marginBottom:"14px"}}>Q<span className='orangeColor'>30</span> to Q<span className='orangeColor'>40</span></h3>
                            <p className='StatsText'>Higher Accuracy</p>
                        </Col>
                        <Col className='d-flex flex-column align-items-center' style={{paddingRight:"2px"}}>
                            <h3 className='StatsHeader'>10<span className='orangeColor'>x</span></h3>
                            <p className='StatsText'>Throughput</p>
                        </Col>
                    </Row>
                </Col>
                <Col xs={1}></Col>
            </Row>
        </div>
    )
}

const LandingPage = () =>{
    return (
        <div className="landingpagebackground">
            <Row>
                <Col xs={10} lg={6} md={6}>
                    <h1 className='heading'>World's First<br/> Commercial Molecular<br/> Computing Solution</h1>
                    <hr className="whiteline" />
                    <p className="landingpagetext">For Diagnostics and Research</p>
                </Col>
                <Col xs={10} lg={6} md={6}>
                </Col>
            </Row>
            <AchStats/>
        </div>
    )
}

export default LandingPage;