import React,{useState} from 'react';
import './style.css';  // We'll create this CSS file in the next step

// import newspaper from "../../assets/images/Frame 3490.png";
import highlightLogo from "../../assets/images/HighlightLogo.png";

import { Container,Row, Col } from 'react-bootstrap';

const BenefitsPage = () =>{

    const [selectedIndex, setSelectedIndex] = useState(0);

    const items = ["IS SINGLE ROUND","IMPROVE CONFIDENCE OF DETECTION","CAN WORK WITH LARGE NUMBER OF TARGETS","compensates for unequal read representation when samples are pooled using barcodes","PROVIDE QUANTATIVE RESULTS","provides unmatched gains"]
    
    const descriptions = [
        <p>Identify positives and negatives in one round<br/><br/>Traditional methods need large 2nd round if positives are identified after 1st round</p>,
        <p>Each sample or marker is tested in at least triplicates for increased optimization and sensitivity</p>,
        <p style={{fontSize:"22px"}}>Pooling based solutions leads to unanticipated repeat testing when multiple pools test positive<br/><br/> Tapestry solution extended to MassSpec and NGS can detect and ID huge number of markers leveraging its compression–decompression stack</p>,
        <p>Each sample or marker is tested in at least triplicates per compression algorithm allowing multiple chances for each samples to generate adequate reads</p>,
        <p>Tapestry’s powerful algorithmic solver uses machine learning techniques to mathematically solve for quantification</p>,
        <p>Using compression-decompression technology allows to better manage signal to noise ratio</p>
    ]

    const [selectedText, setSelectedText] = useState(descriptions[selectedIndex]);

    const handleClick = (index) => {
        setSelectedText(descriptions[index]);
        setSelectedIndex(index)
    };
    return (
        <div className='benefitspagebackground'>
            <Container>
                <Row>
                    <Col xs={2}></Col>
                    <Col xs={8} className="d-flex align-items-center justify-content-center text-center">
                        <h2 className="BenHeadline">Life Science professionals benefit because Tapestry is ...</h2>
                    </Col>
                    <Col xs={2}></Col>
                </Row>
                <Row>
                    <Col>
                        <ul className="yellow-tick-list">
                            {items.map((item,index)=>(
                                <li key={index} onClick={()=>handleClick(index)} style={{opacity:selectedIndex === index?1:0.5,fontWeight:selectedIndex === index?600:400}}>
                                    {item}
                                </li>
                            ))}
                        </ul>
                    </Col>
                    <Col>
                        <div className='description-list'>
                            <Row>
                                <Col xs={2}></Col>
                                <Col xs={8}>
                                    {selectedText && <img src={highlightLogo}/>}
                                </Col>
                                <Col xs={2}></Col>
                            </Row>
                            <Row>
                                <Col xs={2}></Col>
                                <Col xs={8} className="d-flex align-items-center justify-content-center text-center">
                                    {selectedText}
                                </Col>
                                <Col xs={2}></Col>
                            </Row>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default BenefitsPage;