import React from "react";
import "./style.css";

import {Container,Row,Col} from "react-bootstrap";

const IdeasDescriptionPage = () =>{
    return(
        <div className="ideasdescriptionpagebackground">
                <Row className="w-100 justify-content-center text-center">
                    <Col xs={0} md={2} lg={2}></Col>
                    <Col xs={12} md={8} lg={8} className="d-flex align-items-center justify-content-center">
                        <p className="ideasdescriptionHead">ALGORITHMIC BIOLOGICS</p>
                    </Col>
                    <Col xs={0} md={2} lg={2}></Col>
                </Row>                
                <Row className="w-100 justify-content-center text-center">
                    <Col xs={0} md={2} lg={2}></Col>
                    <Col xs={12} md={8} lg={8} className="d-flex align-items-center justify-content-center">
                        <p className="ideasdescriptionText">Molecular Computing is building molecular systems that can respond to their environment in sophisticated ways. We are rediscovering the abilities of biological systems, but this time from an engineer’s lens. We are still early in this journey, and what we are building is to the marvels of biology as a flea is to an elephant. But the field is growing fast. We are seeing another Moore’s law, this time in our ability to program molecules to do our bidding. As we progress on this journey, we will create technology that will help humanity eat better, live healthier, build in a net-zero manner, and get the power to protect our communities, our ecosystems and our planet. Algorithmic Biologics was born out of this ambition.<br/><br/>Algorithmic Biologics is bringing the power of algorithms to biotechnology-based industries by building the information processing layer for biological data. Our founder-CEO is a scientist with two decades of research experience in molecular computing and machine learning, and considered a world leader in his field.<br/><br/>Algorithmic Biologics is at the forefront of the effort to program sophisticated behaviour into molecules by making information-processing networks out of them. Our current effort is pursued  with purified molecules of nucleic acid and protein, developing on ideas that have originated in the field of Molecular Computing. We specialize in writing programs in chemistry and building transformers and other machine learning models with chemicals (instead of traditional silicon) so that reactions in test tubes can perform algorithms. Such smart chemistry will have far reaching applications in molecular testing for diagnostics and research. As the size of these networks increases, the capacity to perform more sophisticated algorithms will emerge with wide ranging applications. In our quest to program chemical systems with sophisticated behaviours that rival and exceed the sophistication of living systems, our mission is to build platforms to write living machines as software applications, with the same ease with which we today write phone applications. By doing this we intend to help humanity live healthier and better.</p>
                    </Col>
                    <Col xs={0} md={2} lg={2}></Col>
                </Row>
        </div>
    )
}

export default IdeasDescriptionPage;